
.fitcontent {
  width: fit-content;
  width: -moz-fit-content;
}
.row-eq-h {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.row-eq-h > [class*='col-'] {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
@media (min-width: 768px) {
.fitcontent-sm {
    width: fit-content;
    width: -moz-fit-content;
}
.row-eq-h-sm {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.row-eq-h-sm > [class*='col-'] {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
}
}
@media (max-width: 768px - 1px) {
.fitcontent-xs {
    width: fit-content;
    width: -moz-fit-content;
}
.row-eq-h-xs {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.row-eq-h-xs > [class*='col-'] {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
}
}
.resume p {
  margin: 0;
}
.resume a:not(.md-button):not(.show-more) {
  color: #2c3e50 !important;
}
.resume .category {
  margin-top: 2em;
}
.resume .list-topics:not(:last-child) {
  margin-bottom: 1.3em;
}
.resume .addtional-information, .resume .experience {
  margin-bottom: 0.8em;
  text-align: justify;
}
.resume .header-info {
  height: auto;
  margin-left: 40px;
  margin-right: 40px;
  /*fine adjustiment*/
  border-radius: 3px;
}
@media (max-width: 768px - 1px) {
.resume .header-info {
      padding-bottom: 0.2em;
}
}
.resume .header-info .left-content, .resume .header-info .right-content {
    position: relative;
    left: -13.33333px;
}
@media (max-width: 768px - 1px) {
.resume .header-info .left-content, .resume .header-info .right-content {
        left: 0px;
}
}
.resume .header-info img {
    height: 170px !important;
    position: relative;
    transform: scale(1.15);
    top: 0px;
    border-radius: 3px;
}
@media (max-width: 768px - 1px) {
.resume .header-info img {
        margin-right: auto;
        margin-left: auto;
        display: block;
        top: -20px;
}
}
.resume .header-info .left-side {
    padding-right: 1em;
}
.resume .category .right-side {
  padding-top: 5px;
  margin-left: 1.5em;
}
@media (max-width: 768px - 1px) {
.resume .category .right-side {
      margin-left: 0px;
}
}
.resume .list-link .button-link {
  display: block;
}
.resume .list-link .button-link:not(:first-of-type) {
  text-align: left;
}
.resume .skills-container {
  margin: 0px auto;
  width: 100%;
}
.resume .skills-container .skill .name {
    text-align: start;
    text-transform: uppercase;
    padding-left: .5em;
    margin: auto 0px;
    display: block;
}
@media (max-width: 768px - 1px) {
.resume .skills-container .skill .name {
        text-align: left;
}
}
.resume .skills-container .skill .rating-container {
    -ms-flex-align: end;
        align-items: flex-end;
}
.resume .skills-container .skill .rating-container .rating {
      margin-right: 1em;
      padding-right: 0px;
}



